ub-menu .link_name {\n  display: none;\n}\n\n.sidebar.close .nav-links li .sub-menu .link_name {\n  font-size: 15px;\n  opacity: 1;\n  display: block;\n}\n\n.sidebar .nav-links li .sub-menu.blank {\n  opacity: 1;\n  pointer-events: auto;\n  padding: 3px 20px 6px 16px;\n  opacity: 0;\n  pointer-events: none;\n}\n\n.sidebar .nav-links li:hover .sub-menu.blank {\n  top: 50%;\n  transform: translateY(-50%);\n}\n\n.sidebar .profile-details {\n  position: fixed;\n  bottom: 0;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  background: #efefef;\n  padding: 6px 0;\n  width: 260px;\n  transition: all 0.5s ease;\n}\n\n.sidebar.close .profile-details {\n  background: none;\n  transition: all 0s ease;\n}\n\n.sidebar.close .profile-details {\n  width: 78px;\n}\n\n.sidebar .profile-details .profile-content {\n  display: flex;\n  align-items: center;\n}\n\n.sidebar .profile-details img {\n  height: 52px;\n  width: 52px;\n  object-fit: cover;\n  border-radius: 16px;\n  margin: 0 14px 0 12px;\n  background: #efefef;\n  transition: all 0.5s ease;\n}\n\n.sidebar.close .profile-details img {\n  height: 40px;\n  width: 40px;\n  padding: 7px;\n}\n\n.sidebar .profile-details .profile_name,\n.sidebar .profile-details .job {\n  color: #959a9e;\n  font-size: 16px;\n  font-weight: 500;\n  /*   white-space: nowrap; */\n  white-space: normal;\n}\n\n.sidebar.close .profile-details .icon,\n.sidebar.close .profile-details .profile_name,\n.sidebar.close .profile-details .job {\n  display: none;\n}\n\n.sidebar .name-job {\n  margin-left: -20px;\n}\n\n.sidebar .profile-details .job {\n  font-size: 12px;\n}\n\n.home-section {\n  position: relative;\n  background: #e4e9f7;\n  height: 100vh;\n  left: 260px;\n  width: calc(100% - 260px);\n  transition: all 0.5s ease;\n}\n\n.sidebar.close ~ .home-section {\n  left: 78px;\n  width: calc(100% - 78px);\n}\n\n.home-section .home-content {\n  height: 60px;\n  display: flex;\n  align-items: center;\n}\n\n.home-section .home-content .icon-menu,\n.home-section .home-content .text {\n  color: #fcfbfd;\n  font-size: 35px;\n}\n\n.home-section .home-content .icon-menu {\n  margin: 0 15px;\n  cursor: pointer;\n}\n\n.home-section .home-content .text {\n  font-size: 26px;\n  font-weight: 600;\n}\n\n.btn-menu {\n  transition: all 0.2s ease;\n}\n\n.sidebar.close .btn-menu {\n  right: -65px !important;\n  transform: rotate(180deg);\n}\n\n.select-page {\n  background: #efefef;\n  border-radius: 20px;\n  padding: 4px 0px;\n  margin: 0 15px;\n}\n\n.select-page .icon {\n  color: #4b4e50;\n}\n\n.select-page .nav_title .link_name {\n  /*  font-weight: 600; */\n  color: #717374;\n}\n\n.select-sub-page {\n  font-weight: 700;\n  color: #2a2b4a !important;\n}\n\n.select-sub-page ion-icon {\n  color: #2a2b4a !important;\n}\n/*# sourceMappingURL=data:application/json;base64, */";